<template>
  <div class="lyr">
    <!-- <button @click="test">test</button> -->
    <!-- <el-row type="flex" justify="center" class="lyric-contain">
      <el-col :span="23" class="song-lyric" :style="lyricMove">
        
        <el-row v-for="(item, index) in lyric" :key="index" :style="{ 'font-size': index == currentRow ? '1.3rem' : '.9rem' }" class="lyric-row">{{ item.text }}</el-row>
      </el-col>
    </el-row> -->
    <lyrics-scroll :lyric="lyric"></lyrics-scroll>
  </div>
</template>

<script>
import axios from "../../network/axios";
import lyricsScroll from "./lyricsScroll";
export default {
  name: "lyrics",
  components: {
    lyricsScroll,
  },
  data() {
    return {
      lyric: [[0, ""]],
      lyricMove: {
        top: "",
      },
      currentRow: 0,
      songList: [],
      // lyrics:[],
    };
  },
  created() {
    this.getLyric(this.$store.state.playId);
  },
  watch: {
    // watch
    // lyricCurrent() {
    //   this.lyric.forEach((element, index) => {
    //     if (this.lyricCurrent == element.time) {
    //       this.lyricMove.top = -index * 2.5 + 6 + "rem";
    //       this.currentRow = index; //
    //     }
    //   });
    // },
    "$store.state.playId"() {
      this.getLyric(this.$store.state.playId);
    },
    // "$store.state.onPlay"() {
    //   this.getLyric(this.$store.state.playId);
    // },
  },

  // created() {
  //   //getSong()
  //   // this.getSong(this.$store.state.playSong); //name
  //   this.getLyric(this.$store.state.searchMusicList[0].musicid);
  //   console.log("this.$store.state.songId:  " + this.$store.state.songId);
  // },

  methods: {
    // getSong(name) {
    //   let url =
    //     "https://api.imjad.cn/cloudmusic/?type=search&search_type=1&s=" +
    //     name +
    //     "&offset=1&limit=10"; //
    //   axios(url)
    //     .then((res) => {
    //       console.log(res.data);
    //       this.formatSongs(res);
    //     })
    //     .catch();
    // },
    // //arr
    // formatSongs(arr) {
    //   let n = arr.length;
    //   for (let i = 0; i < n; i++) {
    //     let obj = {}; //
    //     obj.id = arr[i].id;
    //     obj.song = arr[i].name;
    //     obj.singer = arr[i].ar[0].name;
    //     obj.dt = this.formatDt(arr[i].dt); //ms0000
    //     this.songList.push(obj); //songListv-for
    //   }
    // },
    test() {
      console.log(this.lyric);
      // console.log(this.$store.state.playId);
      // console.log(this.$store.state.playId);
    },
    //idid
    getLyric(id) {
      let url = "https://api.imjad.cn/cloudmusic/?type=lyric&id=" + id; //type=lyric
      // axios(url)
      axios({
        url: "/lyric",
        params: {
          id: id,
        },
      })
        .then(
          (res) => ((this.lyric = []), this.formatLyric(res.data.lrc.lyric))
        )
        .catch(); //formatLyric
    },
    //text
    formatLyric(text) {
      let arr = text.split("\n"); //\n
      let row = arr.length; //
      for (let i = 0; i < row; i++) {
        let temp_row = arr[i]; //"[00:04.302][02:10.00]hello world";
        let temp_arr = temp_row.split("]"); //]
        let text = temp_arr.pop(); //
        //
        temp_arr.forEach((element) => {
          let obj = [[0, ""]];
          let time_arr = element.substr(1, element.length - 1).split(":"); //[
          let s = parseInt(time_arr[0]) * 60 + Math.ceil(time_arr[1]); //currentTime
          obj[0] = s;
          obj[1] = text;
          this.lyric.push(obj); //lyric
        });
      }
      this.lyric.sort(this.sortRule); //
      // this.$store.commit("setLyric", this.lyric); //store
    },
    sortRule(a, b) {
      //
      return a.time - b.time;
    },
  },
};
</script>

<style lang="">
.lyr {
  width: 330px;
}
</style>


<style scoped>
.lyric-contain {
  position: relative;
  height: 500px;
  width: 300px;
  overflow: hidden;
  scrollbar-width: none;
}
.lyric-contain::-webkit-scrollbar {
  display: none;
}
.song-lyric {
  position: absolute;
  height: 25px;
  width: 100%;
}
</style>



