<template>
  <div class="bottomControl" style="z-index: 10000">
    <!--  -->
    <div class="block">
      <audio
        :src="playURL"
        ref="audio"
        autoplay
        @timeupdate="timeupdate"
        @canplay="getDuration"
        @ended="overAudio"
      ></audio>

      <el-slider
        v-model="timeProgress"
        :show-tooltip="false"
        @change="changeProgress"
      ></el-slider>
    </div>
    <div class="fot">
      <!--  -->
      <div class="item item-1">
        <el-button
          @click="drawer = true"
          type="primary"
          style="margin-left: 16px; margin-right: 16px"
        >
          <img :src="picUrl" class="singer" width="100%" />
        </el-button>
        <!--  -->
        <div class="songDetil">
          <span style="font-size: 16px">{{ songName }}</span>
          <br />
          <span style="font-size: 10px">{{ singer }}</span>
        </div>
        <!--   -->
        <div class="bofangye">
          <el-drawer
            style="font-size: 25px"
            :title="title"
            direction="btt"
            size="100%"
            :visible.sync="drawer"
            :with-header="true"
            :append-to-body="true"
          >
            <bo-fang-ye></bo-fang-ye>

            <!--  -->
          </el-drawer>
        </div>
      </div>
      <!--  -->

      <div class="item item-2">
        <div class="bofangcontrol">
          <!-- <audio controls ref="audioPlayer" :src="playURL" autoplay @play="onPlay" @pause="onPlay" @timeupdate="timeupdate"></audio> -->
          <i class="el-icon-caret-left xiayishou" @click="prev"></i>
          <div class="play">
            <i
              class="el-icon-video-play bofang"
              v-show="!playStatus"
              @click="play"
            ></i>
            <i
              class="el-icon-video-pause zanting"
              v-show="playStatus"
              @click="pause"
            ></i>
          </div>
          <i class="el-icon-caret-right shangyishou" @click="next"></i>
          <div class="time">{{ currentText }}/{{ durationText }}</div>
        </div>
      </div>
      <!--  -->

      <div class="item item-3">
        <!-- <button @click="test">test</button> -->

        <bo-fang-list></bo-fang-list>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../../network/axios";
import boFangList from "./boFangList.vue";
import boFangYe from "./boFangYe.vue";
let lastSecond = 0;
// 
let durationNum = 0;
export default {
  name: "bottomControl",
  components: {
    boFangList,
    boFangYe,
  },
  data() {
    return {
      // 
      playStatus: false, //
      Timer: "", //
      currentBar: 0, //0
      durationText: "00:00", //
      listShow: false, //
      loopStyle: "not", //not: list: single: random:
      drawer: false, //
      playURL: this.$store.state.songId, //URL
      picUrl: require("assets/img/kexie.jpg"), //
      songName: this.$store.state.playSong, //
      singer: this.$store.state.playSinger, //
      title: this.$store.state.playSong, //
      // 
      currentTime: 0,
      // 
      timeProgress: 0,
    };
  },
  // //store
  computed: {
    //
    currentText() {
      return this.formatTime(this.currentTime);
    },
    playCnt() {
      let c = 0;
      for (
        let index;
        this.$store.state.playList[0].playDetail[index];
        index++
      ) {
        c++;
      }
      return c;
    },
    //
    playIndex() {
      return this.$store.state.playList[0].playDetail.findIndex(
        (item) => item.playId === this.$store.state.playId
      );
    },
  },
  watch: {
    "$refs.audio.play"() {
      this.$store.state.onPlay = true;
    },
    // "$refs.audio.ended"() {
    //   this.playStatus = false;
    // },
    "$store.state.songId"() {
      this.playURL = this.$store.state.songId;
      this.picUrl = this.$store.state.playPicUrl;
      this.songName = this.$store.state.playSong;
      this.singer = this.$store.state.playSinger;
      this.title = this.$store.state.playSong;
    },
    "$store.state.playId"() {
      this.$store.state.onPlay = !this.$store.state.onPlay;
      this.playStatus = true;
    },
    "$store.state.isDel"() {
      this.next();
    },
    //,
    $route() {
      if (this.$route.name === "onMv") {
        // alert("123");
        this.pause();
      }
    },
  },
  methods: {
    test() {
      console.log(this.$store.state.playList);
    },
    //
    overAudio() {
      // console.log('');
      // this.audioArr.forEach(item=>{
      //     item.isStart = true;
      // })
      // this.$store.state.onPlay = false;
      this.playStatus = false;
      this.next();
    },

    // URL
    getSongUrl(id) {
      this.$store.state.songId =
        "https://music.163.com/song/media/outer/url?" + "id=" + id + ".mp3";
    },
    //
    prev() {
      let t = 0;
      this.playIndex - 1 == -1
        ? (t = this.$store.state.playList[0].playDetail.length - 1)
        : (t = this.playIndex - 1);
      this.playIndexSong(t);
      console.log(this.playIndex);
      console.log(this.$store.state.playList[0].playDetail[t].playSinger);
      console.log(this.$store.state.playSinger);
    },
    //
    next() {
      // alert(123);
      let f = 0;
      //
      this.playIndex + 1 == this.$store.state.playList[0].playDetail.length
        ? (f = 0)
        : (f = this.playIndex + 1);
      this.playIndexSong(f);
      // console.log(this.playCnt);
      // alert(this.$store.state.playList[0].playDetail.length);
    },
    //
    playIndexSong(val) {
      this.$store.state.playSinger =
        this.$store.state.playList[0].playDetail[val].playSinger;
      this.$store.state.playId =
        this.$store.state.playList[0].playDetail[val].playId;
      this.$store.state.playSong =
        this.$store.state.playList[0].playDetail[val].playSong;
      this.$store.state.playPicUrl =
        this.$store.state.playList[0].playDetail[val].playPicUrl;
      this.getSongUrl(this.$store.state.playId);
    },
    // 
    timeupdate() {
      // 
      let time = this.$refs.audio.currentTime;
      // vuex  vuex,,1s
      this.$store.commit("updateCurrentTime", time);
      // console.log(
      //   "this.$store.state.currentTime:" + this.$store.state.currentTime
      // );

      time = Math.floor(time); //Math.floor() 
      if (time !== lastSecond) {
        // console.log(time);
        lastSecond = time;
        this.currentTime = time;
        // 
        this.timeProgress =
          (this.$refs.audio.currentTime / this.$refs.audio.duration) * 100;
        // console.log("this.timeProgress: " + this.timeProgress);
      }
    },

    // 
    changeProgress(e) {
      // console.log(e);
      // 
      durationNum = this.returnSecond(this.durationText);
      this.currentTime = Math.floor((e / 100) * durationNum);
      console.log("changeProgress" + this.currentTime);
      // audio
      this.$refs.audio.currentTime = this.currentTime;
    },
    //
    setTimer() {
      this.clearTimer(); //
      this.Timer = setInterval(this.updateCurrent, 1000);
    },
    //
    clearTimer() {
      //clearInterval()  setInterval()  timeout
      // clearInterval()  setInterval()  ID 
      clearInterval(this.Timer);
      this.Timer = "";
    },
    // 
    returnSecond(time) {
      // split()string.split(separator,limit)separator ("") 
      time = time.split(":");
      let m = parseInt(time[0]); //parseInt() 
      let s = parseInt(time[1]);
      return m * 60 + s;
    },
    //
    formatTime(string) {
      var m = parseInt(string / 60);
      var s = parseInt(string % 60);
      m >= 10 ? m : (m = "0" + m);
      s >= 10 ? s : (s = "0" + s);
      return m + ":" + s;
    },
    //
    play() {
      // this.$refs.audio.ended //
      //   ? this.playSong(this.songContent)
      //   : this.$refs.audio.play(),
      // this.setTimer();
      this.$refs.audio.play();
      this.playStatus = true; //
      // this.$store.state.onPlay = !this.$store.state.onPlay;
    },
    //
    pause() {
      this.playStatus = false;
      this.$refs.audio.pause(), this.clearTimer(); //
      // this.$store.state.onPlay = !this.$store.state.onPlay;
    },
    //
    cutSong(obj) {
      obj == this.songContent
        ? this.play()
        : this.$store.commit("setContent", obj);
    },
    // 
    getDuration() {
      this.durationText = this.formatTime(this.$refs.audio.duration);
    },
  },
};
</script>

<style scoped>
.xiayishou:hover,
.shangyishou:hover {
  color: #ef4444;
}
.time {
  margin-top: 15px;
  /* margin-right: 0px; */
}
.bofang,
.zanting {
  color: #ef4444;
}
.bottomControl {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 75px;
  border-top: #ccc 1px solid;
  /* box-shadow: 1px 1px 1px black; */
  background-color: rgb(255, 255, 255);

  /* display: flex; */
}
.item-1,
.item-3 {
  display: flex; /* vertical-align: middle*/
  align-items: center;
  flex-grow: 1;
  /* background-color: green; */
}

.item-2 {
  /* display: flex;
  align-items: center; */
  flex-grow: 2.5;
  /* background-color: rgb(3, 48, 3); */
  /* border-left: royalblue 1px solid;
  border-right: royalblue 1px solid; */
}
.item-1 .singer {
  margin-left: 10px;
  width: 50px;
  height: 50px;

  border-radius: 5px;
  margin-top: 3px;
}

.shangyishou,
.xiayishou,
.bofang {
  /* margin-right: 20px; */
  flex-grow: 1;
  text-align: center;
}
.bofangcontrol audio {
  width: 100%;
}
.bofangcontrol {
  bottom: 24px;
  left: 36%;
  width: 500px;
  height: 37px;
  /* background-color: pink; */
  display: flex;
  position: absolute;
}
</style>
<style lang="">
.el-slider__button {
  width: 9px;
  height: 9px;
  border: 1px solid #dc2626;
  display: none !important;
}
.el-slider__button-wrapper {
  top: -17px !important;
}
.el-button {
  padding: 0 !important;
  background-color: #fff !important;
  border-style: none !important;
}
span {
  text-align: center;
  /* font-size: 27px; */
}

.songDetil {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100px;
}

.el-slider__bar {
  height: 2px !important;
  background-color: #ef4444 !important;
}
.el-slider__runway {
  top: -1px !important;
  margin: auto !important;
  height: 2px !important;
  margin-bottom: 8px !important;
}
.el-slider__button.hover,
.el-slider__button:hover {
  margin-top: 14px;
  margin-left: 14px;
  cursor: -webkit-grab;
  cursor: grab;
  display: block !important;
  /* top: 10px; */
}
.el-drawer__header {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: #72767b;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0;
  padding: 20px 20px 0;
  background-color: black;
}
.el-drawer__body {
  overflow: hidden !important;
}
</style>


